.cover-container {

    height: 100vh;
    width: 100%;
    object-fit: contain;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.video {
    object-fit: cover;
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: -1;
}

.cover-container h1{

   color: white;
   font-size: 80px;
}

.cover-container p{

    color: white;
    font-size: 32px;
    margin-top: 8px;
 }
 
@media screen and ( max-width:960px ) {
    
    .cover-container > h1 {

        font-size: 70px;
        text-align: center;
    }

    .cover-container > p {

        font-size: 22px;
        text-align: center;
        width: 85%;
    }
}